<template>
    <div class="task7 border">
        <h4 class="header">7.   .         2,      -   3.</h4>
        <p>
            <button @click="powNum(this.number[0])">  {{ number[0] }}</button>
    
            <button @click="powNum(this.number[1])">  {{ number[1] }}</button>
        </p>

    </div>
</template>

<script>
export default {
    name: 'task7', 
    data() {
        return {
            number: [2, 3]
        }
    },
    methods: {
        powNum: function (num) {
            alert(Math.pow(num, 2));
        },
    },
}
</script>

<style scoped>
.header{
    margin-bottom: 10px;
}
button{
    margin: 0 5px;
}
</style>