<template>
    <div>
        <!--     name -->
        <ul> {{ name }}
            <!--        week -->
            <li v-for="elem in week" @click="chooseDay(elem)">{{elem}}</li>
        </ul>
        <!--     question -->
        {{ question }} <input v-model="day" placeholder="  ">
    </div>
</template>

<script>
export default {
    //    
    props: {
        name: String,
        week: Array,
        question: String,
    },
    data() {
        return{
            day: '',
        }
    },
    methods: {
        chooseDay(param) {
            this.day = param;
        }
    }
    
    //    
    // :  
    /* props: {
        title: String,
        likes: Number,
        isPublished: Boolean,
        commentIds: Array,
        author: Object,
        callback: Function,
        contactsPromise: Promise 
    } */
}
</script>

