<template>
    <div class="task15 border">
        <h4 class="header">15.   data    ,   .    18 -    '',    19  25 -    ' ',    26 -    ''.</h4>

        <input v-model="answer" type="text" placeholder="  ">
        <button @click="showAge"> </button>
        <p v-if="age < 18 && visible"></p>
        <p v-else-if="age > 19 && age < 25 &&  visible"> </p>
        <p v-else-if=" visible"></p>
    </div>
</template>

<script>
export default {
    name: 'task15', 
    data() {
        return {
            answer: '',
            age: '',
            visible: false
        }
    },
    methods: {
        showAge: function() {
            this.age = this.answer;
            this.visible = true;
            this.answer = '';
        }
    }
}
</script>

<style scoped>
.header{
    margin-bottom: 10px;
}

</style>