<template>
    <div class="task20 border">
        <h4 class="header">20.     Vue</h4>
        <div class="item" v-for="(item, key, index) in items">
            <p><b>{{key}}</b></p>
            <p v-if="disable[index]">{{ item }}</p>
            <button @click="showInfo(index)"> {{ disable[index] ? '' : '...' }} </button>
        </div>
        
    </div>
</template>

<script>
export default {
    name: 'task20', 
    data() {
        return {
            disable: [false, false, false],
            items: {
                HTML: ' ,       -   . ,       ,         .    ,       HTML   .', 
                CSS: ' ,       -.  CSS   ,      .      :       ?   ,        ?    -      ?', 
                JavaScript: '  ,      - (: ,          ,  , ).                ,    .',
            },
        }
    },
    methods: {
        showInfo: function(num) {
            this.disable[num] = !this.disable[num];
        }
    }
}
</script>

<style scoped>
.header{
    margin-bottom: 10px;
}

.item{
    margin-bottom: 15px;
}

button{
    width: 100px;
    height: 25px;
    margin-top: 5px;
}

</style>