<template>
    <div class="task10 border">
        <h4 class="header">10.  .     .  ,          .</h4>
       <a @click.once.prevent="clickLink" href="#">click</a>
       <p>{{ counter }}</p>

    </div>
</template>

<script>
export default {
    name: 'task10', 
    data() {
        return {
            counter: 0,
        }
    },
    methods: {
       clickLink: function () {
        this.counter += 1;
       },
    },
}
</script>

<style scoped>
.header{
    margin-bottom: 10px;
}
</style>